
.head{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

}


.card-head{
border:  1px solid black;
border-radius: 20px;
padding: 40px;
width: 400px;
background-color: #917777;
position: relative;

}

.img-background{
  height: 100vh;
  width: 100%;
  position: absolute;
}

.link-text{
  color: #fff;
  text-decoration: none;
  
}
.link-text:hover{
  color:#fff;
  text-decoration: none;
}

/* ============================== LayOut Page ============================== */

.layout-head{
  display: flex;
  width: 100%;
}
.menue-bar{
height: 100vh;
width: 20%;
background-color: rgb(62, 15, 15);
margin: 5px;
}

.header-bar{
border:  1px solid black;
width: 1350px;
margin: 5px;
padding: 25px;
}

.layout-body{
  height: 90vh;
  border:  1px solid black;
  margin: 5px;
}
.menue{
  text-decoration: none;
  color: white;
  font-size: 28px;
  padding: 20px;
 
}



.spinner-bacground{
  background-color: rgb(215, 200, 200 ,0.3);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menue1{
  color: white;
  font-size: 20px;
 
  
}

.menue-item{
  color: #fff;
  font-size: 28px;
padding: 10px;
}
.menue1:hover{
  background-color: #fff;
  color: rgb(62, 15, 15) ;
  direction: none;
}

.menue-item:hover{
  background-color: #fff;
  color: rgb(62, 15, 15) ;
  direction: none;
}

.header-notification{
  display: flex;
  align-items: center;
  justify-content: end;
}

.header-notification a{
  color: black;
  font-size: 24px;
}

.menue-item i{
  font-size: 34px;
}
.menue-item a{
  color: #fff;
  font-size: 28px;
}
.menue-item:hover a{
  color: rgb(62, 15, 15);
 
}